<template>
	<div>
		<Input v-model="user_id" placeholder='username'>
		<span slot="prepend"></span>
		<Button slot="append" :loading="getLoading" >
			<vueTencentCaptcha appid='2081307488' @callback="getForgetKey"></vueTencentCaptcha>
		</Button>
		</Input>
		<Input v-model="email_key" placeholder='email_key' style="margin-top:10px">
		<span slot="prepend">{{hint}}</span>
		<Button slot="append" @click="verifyEmailKey"></Button>
		</Input>
	</div>
</template>

<script>
	export default {
		name: 'ForgetPassword',
		data() {
			return {
				user_id: '',
				email_key: '',
				getLoading:false,
				hint:''
			}
		},
		methods: {
			getForgetKey: function(ticket) {
				if (!ticket.ret) {
					let uPattern = /^[a-zA-Z0-9]{3,20}$/;
					if (uPattern.test(this.user_id)) {
						this.getLoading=true;
						this.hint=''
						this.$Message.info('');
						var params = new URLSearchParams();
						params.append('ticket', ticket.ticket);
						params.append('randStr', ticket.randstr);
						params.append('user_id', this.user_id);
						axios
							.post(this.$store.state.API_ROOT + 'user/password', params)
							.then(response => {
								if (response.data.data.is_ok) {
									this.$Message.success(response.data.data.msg === '' ? '' : response.data.data.msg)
								} else {
									this.$Message.error(response.data.data.msg === '' ? '' : response.data.data.msg)
								}
								this.getLoading=false;
							}).catch(function(error) {
								this.$Message.error('');
								this.getLoading=false;
							});

					} else {
						this.$Message.error('');
					}
				}
			},
			verifyEmailKey() {
				if (this.email_key.length != 6) {
					this.$Message.error('6');
				} else {
					this.$Message.info('');
					var params = new URLSearchParams();
					params.append('user_id', this.user_id);
					params.append('email_key', this.email_key);
					axios
						.post(this.$store.state.API_ROOT + 'user/' + this.user_id + '/password', params)
						.then(response => {
							if (response.data.data.is_ok) {
								this.email_key = response.data.data.password
								this.hint=''
								this.$Message.success("" + response.data.data.password);
							} else {
								this.$Message.error('');
							}
						}).catch(function(error) {
							this.$Message.error('');
						});
				}
			}
		}
	}
</script>

